.header {
  width: 100%;
  height: 80vh;
  @include bgImage("../../images/1.jpg");
  background-color: rgba(3, 17, 43, 0.288);
  @include flexCenter;
  color: $white;
  &__content {
    text-align: center;
    &-links a {
      text-decoration: none;
      color: $white;
      margin-left: 15px;
      font-size: 20px;
    }
    &-span {
      display: inline-block;
      height: 15px;
      width: 2px;
      background: $white;
      margin-left: 20px;
      transform: rotate(10deg);
    }
  }

  &__content-h1 {
    font-size: 75px;
    margin-bottom: 20px;
    letter-spacing: 1px;
    font-weight: 600;
  }
}
