.about {
  padding: 100px 0;
  width: 100%;
  @include responsive(765px) {
    margin-bottom: 0;
  }
  &__h4 {
    font-size: 50px;
    font-weight: 600;
    padding-top: 20px;
    padding-left: 70px;
    @include responsive(765px) {
      padding-left: 30px;
    }
  }
  &__text {
    color: $silver;
    font-size: 15px;
    font-weight: 500;
    line-height: 2;
    padding-left: 100px;
    @include responsive(765px) {
      padding-left: 30px;
    }
  }
  &__section {
    background: $gray;
    padding: 60px 8px;
    text-align: center;
    border-radius: 20px;
    @include responsive(765px) {
      margin: 30px 60px;
      padding: 60px 20px 80px 20px;
    }
    &-icon {
      color: $blue;
      margin-bottom: 10px;
    }
    &-heading h6 {
      font-size: 16px;
      font-weight: 550;
      margin-bottom: 15px;
    }
    &-text {
      color: $silver;
      font-size: 14px;
      letter-spacing: 1px;
    }
  }
}
