.navbar {
  width: 100%;
  position: fixed;
  top: 0;
  transition: all 0.5s linear;
  height: 80px;
  z-index: 100;
  

  &__content {
    display: flex;
    justify-content: space-between;
    height: 80px;
    align-items: center;
  }
  &__left img {
    width: 80px;
    height: auto;
  }
  &__right {
    display: flex;
    list-style: none;
    & li a {
      color: $white;
      text-decoration: none;
      font-size: 16px;
      font-weight: 500;
      padding: 10px 30px;
    }
  }
}
