.show {
  @include bgImage("../../images/1.jpg");
  @include flexVertical;
  width: 100%;
  height: 500px;
  background-color: rgba(36, 35, 35, 0.768);
  color: $white;
  @include responsive(765px) {
    padding: 10;
  }

  &__content h2 {
    font-size: 2.5rem;
  }
  &__card {
    position: absolute;
    bottom: -250px;
    width: 470px;
    right: 0;
    padding: 80px 40px;
    border-radius: 10px;
    background: $blue;
    color: $black;
    @include responsive(765px) {
      margin-right: 20px;
      margin-top: 0px;
      top: 131px;
      bottom: -300;
      padding-top: 20px;
      padding-bottom: 90px;
    }
    &-h6 {
      font-size: 12px;
      font-weight: 400;
      letter-spacing: 3px;
      margin-bottom: 20px;
    }
    &-h4 {
      font-size: 30px;
      font-weight: 600;
      color: rgb(36, 2, 2);
      margin-bottom: 10px;
    }
    &-text {
      font-size: 15px;
      letter-spacing: 2px;
    }
  }
}
