.portfolio {
  width: 100%;
  height: 500px;
  position: relative;
  @include responsive(765px) {
    margin-top: 900px;
  }
  &::before {
    position: absolute;
    content: "";
    width: 50%;
    height: 100%;
    background: $white;
    top: 0;
    left: 0;
    z-index: -1;
  }
  &::after {
    position: absolute;
    content: "";
    width: 50%;
    height: 100%;
    background: $silver;
    top: 0;
    right: 0;
    z-index: -1;
    @include responsive(765px) {
      display: none;
    }
  }
  &__section {
    @include flexVertical;
    height: 500px;
    width: 100%;
    z-index: 5;
    @include responsive(765px) {
      padding-right: 15px;
    }
  }
  &__content {
    @include responsive(765px) {
      margin-top: 260px;
      padding-left: 55px;
      padding-bottom: 20px;
    }
    &-h6 {
      color: $blue;
      font-size: 17px;
      font-weight: 400;
      letter-spacing: 14px;
      text-transform: uppercase;
      margin-bottom: 5px;
      @include responsive(765px) {
        color: $white;
      }
    }
    &-h3 {
      font-size: 40px;
      font-weight: 700;
      text-transform: uppercase;
      letter-spacing: 4px;
      text-shadow: 2px 8px 10px #074085;
    }
  }
  &__card {
    background: $blue;
    text-align: center;
    border-radius: 10px;
    padding-bottom: 5px;
    @include responsive(765px) {
      margin: 10px 0 30px 0;
    }
    &-img img {
      padding-top: 5px;
      border-radius: 10px;
      width: 95%;
      height: auto;
      @include responsive(765px) {
        padding-top: 10px;
        border-radius: 20px;
        width: 96%;
        height: auto;
      }
    }
  }
  &-name h5 {
    font-size: 30px;
    padding: 5px auto;
    @include responsive(765px) {
      padding: 10px;
    }
  }
  &-expert {
    font-size: 12px;
    color: $silver;
  }
}
